import React from "react";
import Link from "gatsby-link";
import Preview from '../../components/Preview'
import Helmet from "react-helmet";
import FontAwesomeIcon from '@fortawesome/react-fontawesome';
import faAngleLeft from '@fortawesome/fontawesome-free-solid/faAngleLeft';

export default function Smit({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">
      <Helmet title={`Инвентарь - Смитт`} />
      <div className="page-top-banner">
        <h2 className="page-heading" style={smitStyle}> Упражнения в тренажере Смита</h2>
        <Link to="/" className="page-btn-back"><FontAwesomeIcon icon={faAngleLeft} className="preview-btn-icon" /> Все упражнения</Link>
    </div>
      {posts
        .filter(post => post.node.frontmatter.filterplace === 'smit')
        .map(({ node: post }) => {
          return (
            <Preview post={post} />
          );
        })}
    </div>
  );
}

const smitStyle = {
	backgroundImage: 'url(https://bodysolid-russia.ru/ckfinder/userfiles/images/smith_machine_002.jpg)',
	backgroundPosition: "center"
};

export const pageQuery = graphql`
  query SmitQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
            filterplace
          }
        }
      }
    }
  }
`;



